<template>
  <div class="store-index">
    <div class="crumbs-box ">
      <span class="crumbs">店铺管理</span>
      <span class="crumbs line">/</span>
      <span class="crumbs">店铺列表</span>
    </div>
    <div class="content-page">
      <div class="table-header">
        <div class="table-header-left">
          <div class="search-form-box">
            <label for="" class="search-label">店铺名称</label>
            <input type="text" v-model="store_name" class="admin-input" placeholder="填写店铺名称">
          </div>
          <div class="admin-btn flex-row search-btn hover" @click="searchList"><img src="../../assets/ic_shousuo.png" alt="">搜索</div>
        </div>
        <div class="table-header-right">
          <div class="admin-btn flex-row hover" @click="addStorePage"><img src="../../assets/ic_add_icon.png" class="admin-add-icon" >添加店铺</div>

        </div>
      </div>
      <div class="admin-table" style="width: 100%">
        <el-table :data="tableData" style="width: 100%" :header-cell-style="tableHeaderStyle">
          <el-table-column prop="id" label="序号" width="80"></el-table-column>
          <el-table-column prop="store_name" label="店铺名称" width=""></el-table-column>
          <el-table-column prop="cover" label="店铺LOGO" width="">
            <template slot-scope="scope">
              <img :src="host+scope.row.cover" class="store-cover" v-if="scope.row.cover">
            </template>
          </el-table-column>
          <el-table-column prop="addr" label="所属商圈" width="">
            <template slot-scope="scope">
              <p>{{scope.row.province+scope.row.city}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="address" label="线下门店地址" width="300"></el-table-column>
          <el-table-column prop="create_time" label="申请时间" width=""></el-table-column>
          <el-table-column prop="status" label="状态" width="">
            <template slot-scope="scope">
              <p v-if="scope.row.sh_status==0" class="table-status-color">审核中</p>
              <p v-else-if="scope.row.sh_status==1&&scope.row.status==1&&scope.row.store_status==1" class="table-status-color">运营中</p>
              <p v-else-if="scope.row.sh_status==-1" class="table-status-color">审核已拒绝</p>
              <p v-else-if="scope.row.status==2" class="table-status-color">已关闭</p>
              <p v-else-if="scope.row.store_status!=1" class="table-status-color">已封店</p>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <div class="table-btn hover2" @click="storeDetailsFn(scope.row)">查看详情</div><!--  v-if="scope.row.sh_status!=1"  -->
              <div class="table-btn hover2" v-if="scope.row.sh_status==1&&scope.row.status==1&&scope.row.store_status==1" @click="goLogin">登录店铺查看</div> <!-- 运营中 退出登录 -->
              <div class="table-btn hover2" v-if="scope.row.sh_status==1&&scope.row.status==1&&scope.row.store_status==1" @click="closeStoreLayerShow(scope.row)">关闭</div><!-- 运营中 -->
              <div class="table-btn hover2" v-if="scope.row.sh_status==1&&scope.row.status==2&&scope.row.store_status==1" @click="yesStoreLayerShow(scope.row)">启用</div><!-- 运营中 -->
              <div class="table-btn hover2" v-if="scope.row.sh_status!=1&&scope.row.store_status!=1" @click="goRoleIndex">开账号</div> <!-- 账号管理-创建账号 -->
            </template>
          </el-table-column>
        </el-table>
        <div class="page-box" v-if="tableData.length>0">
          <el-pagination
            @current-change="handleCurrentChange"
            background
            layout="prev, pager, next"
            :total="totalPage"
            :page-size="10">
          </el-pagination>
        </div>
      </div>
    </div>
    <!-- 关闭店铺 -->
    <div class="layer-box-page " id="closeStoreLayer">
      <div class="scroll-box">
        <div class="layer-content-page" style="width: 800px;">
          <div class="layer-title-box">
            <div class="title">关闭店铺</div>
            <img src="../../assets/layer_close_icon.png" class="layer-close-icon hover" @click="closeLayer">
          </div>
          <div class="layer-content box-sizing">
            <div class="flex-col">
              <img src="../../assets/bj_guanbidianpu.png" class="bg-close-store">
              <div class="close-store-tops flex-col">
                <p>关闭店铺，该店铺将不会在超神宿平台再展示；</p>
                <p>但您的店铺数据仍在后台店铺管理处查看。</p>
                <p>您确定要关闭该店铺吗？</p>
              </div>
              <div class="confirm-btn hover" @click="confirmCloseStore()">确认</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

  import { Storelist, StoreEditStatus } from '@/axios/api'
  import { mapState } from 'vuex'
  export default {
    name: 'store-index',
    data() {
      return {
        token: '',
        tableHeaderStyle: {
          'background': '#FAFAFA',
          'height': '76px'
        },
        nowItem: {}, // 点击当前店铺info
        tableData: [],
        totalPage: 0,
        page: 1,
        store_name: '' // 搜索
      }
    },
    computed: {
      ...mapState(["host", "userinfo"])
    },
    components: {

    },
    created() {
      this.$store.state.navActive = this.$route.query.navActive;
      // 获取登录着的信息，得不到就循环获取;在app-view.vue中存的token
      this.getLoginInfo();
    },
    methods: {
      getLoginInfo() {
        let token = sessionStorage.getItem('token');
        if (token) {
          this.token = token;
          this.getData();
        } else {
          this.getLoginInfo()
        }
      },
      getData() {
        Storelist({
          token: this.token,
          store_name: this.store_name,
          page: this.page
        }).then(res => {
          // console.log(res);
          if (res.status == 1) {
            let list = res.data.list;
            if (list.length > 0) {
              for(let i in list) {
                list[i].create_time = this.createTime(list[i].create_time)
              }
            }
            this.tableData = list;
            this.totalPage = res.data.total
          } else {
            this.$message.error(res.msg)
          }
        })
      },
      closeStoreLayerShow(item) {
        let status = this.util.power('1', '4');
        if (status) {
          this.nowItem = item;
          $("#closeStoreLayer").fadeIn(100);
        } else {
          this.$message.error('暂无权限')
        }
      },
      // 确认关闭店铺
      confirmCloseStore() {
        StoreEditStatus({
          token: this.token,
          store_id: this.nowItem.id,
          status: this.nowItem.status==1?2:1
        }).then(res => {
          if(res.status == 1){
            this.closeLayer()
            this.$message.info('已关闭');
            this.getData()
          } else {
            this.$message.error(res.msg)
          }
        })
      },
      // 启用店铺
      yesStoreLayerShow(item) {
        let status = this.util.power('1', '4');
        if(status) {
          StoreEditStatus({
            token: this.token,
            store_id: item.id,
            status: item.status==1?2:1
          }).then(res => {
            if(res.status == 1){
              this.$message.info('已启用');
              this.getData()
            } else {
              this.$message.error(res.msg)
            }
          })
        } else {
          this.$message.error('暂无权限')
        }

      },
      closeLayer() {
        this.nowItem = {};
        $("#closeStoreLayer").fadeOut(100);
      },
      // 分页
      handleCurrentChange(curr) {
        // console.log(curr);
        this.page = curr;
        this.getData()
      },
      searchList() {
        this.page = 1;
        this.getData()
      },
      // 添加店铺
      addStorePage() {
        let status = this.util.power('1', '2');
        if (status) {
          if (this.userinfo.type == 1) { // 普通店铺
            this.changePage('/storeAddEdit', {})
          } else {
            this.changePage('/storeAddEdit_vip', {}) // 会员商城 添加店铺
          }
        } else {
          this.$message.error('暂无权限');
        }

      },
      // 查看详情
      storeDetailsFn(item) {
        let status = this.util.power('1', '1');
        if (status) {
          if(item.type == 1) {
            // 普通店铺
            this.changePage('/storeDetails', {storeId: item.id})
          } else {
            // 会员店铺
            this.changePage('/storeDetails_vip', {storeId: item.id})
          }
        } else {
          this.$message.error('暂无权限');
        }

      },
      // 开账号
      goRoleIndex() {
        let grouplist = JSON.parse(this.userinfo.grouplist);
        if (grouplist) {
          for(let i in grouplist) {
            if (grouplist[i].id == 2) {
              this.changePage('/roleIndex', {navActive: '2-0'})
              break
            } else {
              if (i >= grouplist.length - 1 && grouplist[i].id != 2) {
                this.$message.error('暂无权限');
              }
            }
          }
        } else {
          this.changePage('/roleIndex', {navActive: '2-0'})
        }

      },
      // 去登陆
      goLogin() {
        sessionStorage.clear();
        window.location.href = config.host+'/pt/enterExplain.html';
      },
      addTime(m){return m<10?'0'+m:m },
      createTime(value){
        let time = new Date(value*1000);
        let year = time.getFullYear();
        let month = time.getMonth()+1;
        let date = time.getDate();
        let hours = time.getHours();
        let minutes = time.getMinutes();
        let seconds = time.getSeconds();
        return year+'-'+this.addTime(month)+'-'+this.addTime(date)+' '+this.addTime(hours)+':'+this.addTime(minutes)+':'+this.addTime(seconds);
      },//2017-05-08 10:31:27

    }
  }
</script>
<style lang="less" scoped>

</style>



